.section-2 {
    height: 100%;
    width: 100%;
    background-color: var(--c-secondary);
    z-index: 1;
   
        &.fadeUppText{
            .section-2{
                &__content-1 {
                    opacity: 1;
                    transform: translateY(0) !important;
                }
            }
        }
    
    &__content-1 {
        
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        padding-left: 120px;
        height: 100%;
        width: 80%;
        color: #fff;
        z-index: 2;
        position: relative;
        opacity: 0;
        transition: 0.7s ease-in-out;
        transform: translateY(50px);
        &__title{
            margin-bottom: 50px !important;
            z-index: 2;
        }
        &__descr{
            z-index: 1;
            font-size:  var(--fz-m);
            margin-bottom: 25px;

            &:last-child{
                margin-bottom: 95px;
                z-index: 2;
            }
        }
    }
    .intro-slider{
        
        &__wrapper{
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: row;

            &-left{
                position: relative;
                width: 36%;
                height: 100%;
                background: #fff;
                padding-right: 50px;
                padding-left: calc(((100% - 1500px) / 2));
                z-index: 100;
                
                
                .slick-slide{
                    min-height: 75px;
                }
                .slick-arrow{
                    margin: 0 auto;
                }
            }
            &-right {
                width: 64%;
                height: 100%;

                &-swiper {
                    width: 100%;
                    height: 100%;
                }
                &-slide{
                    height: 100%;
                    img{
                        object-fit: cover;
                            height: 100%;
                            width: 100%;
                    }
                }
                .slick-slider {
                    height: 100%;
                }
                .slick-list.draggable{
                    height: 100% !important;
                }
                .slick-track{
                    height: 100%;
                }
            }
            .slick-list.draggable{
                overflow: visible;
            }
        }
    }
    
    &__slide{
        position: absolute;
        -o-object-fit: cover;
        object-fit: cover;
        height: 100%;
        width: 100%;

        
    }
    &-container {
        height: 100%;
        display: flex;
        max-width: 100%;
        align-items: flex-start;
        &__content {
            position: relative;
            margin-top: 190px;
            max-width: 100%;

            .circle{
                display: none;
            }
        }
    }
    &-list {
        min-width: 0;
        overflow: hidden;
        height: 600px;
       
        .slick-list{
            padding-top: 30px;
        }
        &__item {
            height: 85px;
            position: relative;
            transition: all 0.5s ease;
            cursor: pointer;
            &:hover{
                transform: translate(2px);
            }
            span{
                color: #555555;
                text-transform: uppercase;
                padding: 10px 10px 10px 0;
                background: #fff;
                z-index: 4;
                display: inline-block;
                
            }
            &.slick-active.slick-current{
                .section-2-list__container {
                    position: relative;
                    z-index: 101;
                    &::before{
                        width: 100%;
                        transition: width 2s ease-out;
                        transition-delay: 0.3s;
                    }
                    
                }
                span{
                    font-weight: 700;
                    position: relative;
                    
                }
            }
        }
        
        &__container{
            position: relative;
            &::before{
                width: 0%;
                content: "";
                position: absolute;
                right: 0;
                top: 0.90em;
                transform: translateY(-50%);
                height: 1px;
                z-index: 3;
                background-color: var(--c-secondary);
                font-weight: 400;
            }
        }
        &__link {
            font-size:  var(--fz-xs);
            text-transform: uppercase;
            
        }
    }
    &__wrpaper {

        &-right{
            max-width: 100%;
            height: 100%;
            &-slide {
                height: 100%;
            }
        }
    }
    .circle {
        position: absolute;
        right: -2%;
        top: 8%;
        transform: translate(100% ,-50%);
        display: flex;
        align-items: center;
        transition: opacity 0.3s linear;
        // opacity: 0;
        // visibility: hidden;
        &-container {
            border-radius : 50%;
            width: 80px;
            height: 80px;
            border: 3px solid #fff;
            overflow: hidden;
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &-text{
            font-weight: 700;
            font-size: 12px;
            color: #fff;
            background: none;
            margin-left: 20px;
            text-transform: uppercase;
        }
        &.show{
            transition: opacity 0.5s ease-in-out;
            opacity: 1;
            visibility: visible;
        }
    }
    .circle-right {
        
        position: absolute;
        left: -150px;
        bottom: 30px;
        transform: translate(100% ,-50%);
        display: flex;
        align-items: center;
        transition: opacity 0.3s linear;
        // opacity: 0;
        // visibility: hidden;
        display: none;
        &-container {
            border-radius : 50%;
            width: 80px;
            height: 80px;
            border: 3px solid #fff;
            overflow: hidden;
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &-text{
            font-weight: 700;
            font-size: 12px;
            color: #fff;
            background: none;
            margin-left: 20px;
            text-transform: uppercase;
        }
        &.show{
            transition: opacity 0.5s ease-in-out;
            opacity: 1;
            visibility: visible;
        }
    }
}
@media (max-height: 600px) {
    .section-2{

        &-container {
            &__content {
                margin-top: 100px !important;
            }
        }
    }
}